.despesa-page {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.add-button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px 20px;
  margin-bottom: 20px;
  cursor: pointer;
}

.filters {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.filter-item {
  display: flex;
  align-items: center;
  margin-right: 10px;
}

.filter-item label {
  margin-right: 5px;
}

.filter-item input,
.filter-item select {
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.filter-button {
  background-color: #28a745;
  color: #fff;
  border: none;
  padding: 8px 15px;
  margin-top: 5px;
  border-radius: 5px;
  cursor: pointer;
}

.despesa-table {
  width: 100%;
  border-collapse: collapse;
}

.despesa-table th,
.despesa-table td {
  border: 1px solid #ccc;
  padding: 10px;
  text-align: center;
}

.despesa-table th {
  background-color: #f2f2f2;
}

.page-button {
  background-color: #f2f2f2;
  color: #007bff;
  border: 1px solid #ccc;
  padding: 5px 10px;
  margin-right: 5px;
  cursor: pointer;
}

.page-button.active {
  background-color: #007bff;
  color: #fff;
}

.total-registros {
  margin-top: 10px;
  text-align: right;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  max-width: 400px;
}

.modal h2 {
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
}

.form-group select,
.form-group input[type="text"],
.form-group input[type="date"] {
  width: 100%;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.form-group button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 8px 15px;
  margin-right: 5px;
  border-radius: 5px;
  cursor: pointer;
}

.form-group button:last-child {
  background-color: #dc3545;
}

.form-group button:hover {
  opacity: 0.8;
}


.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  max-width: 400px;
  width: 100%;
}

.modal h2 {
  margin-top: 0;
}

.modal form {
  display: flex;
  flex-direction: column;
}

.modal form .form-group {
  margin-bottom: 10px;
}

.modal form .form-group label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
}

.modal form .form-group input,
.modal form .form-group select {
  width: 100%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.modal form button {
  padding: 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  background-color: #007bff;
  color: #fff;
  font-size: 16px;
  margin-top: 10px;
}

.modal form button[type="button"] {
  background-color: #ccc;
  margin-right: 10px;
}